<template>
  <div>
    <el-form label-width="80px" :model="myUserInfo" ref="myUserInfo">
      <el-form-item label="">
        <el-upload
          class="my-uploader"
          action
          :auto-upload="false"
          :show-file-list="false"
          :on-change="changeAvatar"
          accept=".jpg, .jpeg, .png, .gif, .bmp, .pdf, .JPG, .JPEG, .PBG, .GIF, .BMP, .PDF"
        >
          <img v-if="myUserInfo.avatar" :src="myUserInfo.avatar" class="uploaderImg" />
          <i v-else class="el-icon-plus my-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item
        label=""
        prop="email"
        :rules="[
          {
            type: 'email',
            message: '',
            trigger: ['blur', 'change']
          }
        ]"
      >
        <el-input v-model="myUserInfo.email"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-radio v-model="myUserInfo.sex" label=""></el-radio>
        <el-radio v-model="myUserInfo.sex" label=""></el-radio>
        <el-radio v-model="myUserInfo.sex" label=""></el-radio>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm"></el-button>
        <el-button @click="reSetFrom"></el-button>
      </el-form-item>
    </el-form>
    <el-dialog
      title=""
      width="500px"
      :visible.sync="dialogVisible"
      append-to-body
      :close-on-click-modal="false"
    >
      <div class="dialog-cropper-center">
        <div class="cropper-content">
          <div class="cropper">
            <vueCropper
              ref="cropper"
              :img="option.img"
              :outputSize="option.size"
              :outputType="option.outputType"
              :info="true"
              :full="option.full"
              :canMove="option.canMove"
              :canMoveBox="option.canMoveBox"
              :original="option.original"
              :autoCrop="option.autoCrop"
              :fixed="option.fixed"
              :fixedNumber="option.fixedNumber"
              :centerBox="option.centerBox"
              :infoTrue="option.infoTrue"
              :fixedBox="option.fixedBox"
              @realTime="realTime"
            ></vueCropper>
          </div>
        </div>
        <div :style="previewStyle" class="previewStyle">
          <div :style="previews.div">
            <img :src="previews.url" :style="previews.img" />
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="finish" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import { VueCropper } from "vue-cropper";

export default {
  data() {
    return {
      img: "",
      fullscreenLoading: false,
      myUserInfo: {
        avatar: "",
        email: "",
        sex: ""
      },
      previews: {},
      dialogVisible: false,
      // option
      option: {
        img: "", // 
        info: true, // 
        outputSize: 0.8, // 
        outputType: "jpeg", // 
        canScale: false, // 
        autoCrop: true, // 
        // autoCropWidth: 300, // 
        // autoCropHeight: 200, // 
        fixedBox: false, //  
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: true, // 
        canMoveBox: true, // 
        original: false, // 
        centerBox: false, // 
        infoTrue: true // true  false 
      },
      previewStyle: {},
      picsList: [] //
      // 
    };
  },
  components: {
    VueCropper
  },
  created() {
    if (this.userInfo) {
      this.reSetFrom();
    }
  },
  methods: {
    ...mapMutations(["saveUserextInfo"]),
    reSetFrom() {
      this.myUserInfo.avatar = this.userInfo.ext_info.yesapi_avatar || "";
      this.myUserInfo.email = this.userInfo.ext_info.yesapi_email || "";
      this.myUserInfo.sex = this.userInfo.ext_info.yesapi_sex || "";
    },
    changeAvatar(file) {
      const isLt1M = file.size / 1024 / 1024 / 2 < 1;
      if (!isLt1M) {
        this.$message.error(" 500k!");
        return false;
      }
      this.fileinfo = file;

      // 
      this.$nextTick(() => {
        this.option.img = URL.createObjectURL(file.raw);
        this.dialogVisible = true;
      });
    },
    // 
    realTime(data) {
      this.previews = data;

      this.previewStyle = {
        width: data.w + "px",
        height: data.h + "px",
        overflow: "hidden",
        margin: "0",
        zoom: 100 / data.w
      };
    },
    //base64,
    finish() {
      this.$refs.cropper.getCropData(async data => {
        console.log(data);
        this.fullscreenLoading = true;
        let newAvatar = await this.$api
          .uploadImgByBase64(data, "avatar")
          .catch(err => {
            return err;
          });
        if (newAvatar.ret == 200 && newAvatar.data.err_code == 0) {
          this.dialogVisible = false;
          this.myUserInfo.avatar = newAvatar.data.url;
        } else {
          this.$message({
            message: ",",
            type: "error"
          });
        }
        this.fullscreenLoading = false;
      });
    },
    submitForm() {
      this.$refs.myUserInfo.validate(async valid => {
        if (valid) {
          this.fullscreenLoading = true;
          let userUpdateExtInfo = await this.$api
            .userUpdateExtInfo(
              this.myUserInfo.avatar,
              this.myUserInfo.sex,
              this.myUserInfo.email
            )
            .catch(err => {
              return err;
            });

          if (
            userUpdateExtInfo.ret == 200 &&
            userUpdateExtInfo.data.err_code == 0
          ) {
            this.$message({
              message: "!",
              type: "success"
            });
            this.saveUserextInfo(userUpdateExtInfo.data.ext_info);
          } else {
            this.$message({
              message: "",
              type: "error"
            });
          }
          this.fullscreenLoading = false;
        } else {
          return false;
        }
      });
    }
  },
  watch: {
    userInfo() {
      this.reSetFrom();
    }
  },
  computed: {
    ...mapState(["userInfo"])
  }
};
</script>

<style scoped>
@import "../assets/css/base.css";

.el-input {
  width: 300px;
}
.dialog-cropper-center {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.cropper {
  width: 300px;
  height: 300px;
}

.previewStyle {
  border-radius: 50%;
  display: inline-block;
  /* margin-left: 50px; */
}
</style>
